import { url, } from '@/configs/global'
import model from './__model__'

class ReportComentario extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'report_comentario'
        this.allowRemove = true

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['report_id', ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_report_note`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_report_note/${this.id}`, obj)
    }

    async delete({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        obj.deleted = true
        return await this.axios.post(`${url}/api/${id_e}/update_report_note/${this.id}`, obj)
    }
}

export default ReportComentario