import { url, } from '@/configs/global'
import ReportComentario from './report_comentario'
import model from './__model__'

class Report extends model{

    static DADOS = [
        {subtitle: 'finalizados', color: 'var(--verde)', number: (resume) => resume.total_finalizado, icon: 'report', generalState: 0},
        {subtitle: 'em aberto', color: 'var(--laranja)', number: (resume) => resume.total_em_aberto, icon: 'report', generalState: 1},
        {subtitle: 'cancelados', color: '#181a21', number: (resume) => resume.total_cancelado, icon: 'report', generalState: 2},
    ]

    constructor(data, axios, getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'report'
        this.model_name = (getters['empresa.get'].segment.reports).description
        this.allowRemove = true
        //this.is_model= false
        this.bug_select_keys = ['ativo','service']
        this.bug_select()

        if(this.criticality == 'Baixa')      this.criticidade = '1: Baixa'
        if(this.criticality == 'Média')      this.criticidade = '2: Média'
        if(this.criticality == 'Importante') this.criticidade = '3: Importante'
        if(this.criticality == 'Urgente')    this.criticidade = '4: Urgente'

        if (this.notes)    this.notes    = this.notes.map((e)=>new ReportComentario(e, this.axios))

        this.listaCriacao = [
            {label: 'model', type: 'switch', labelInput: 'Carregar um modelo?', permission: '100', 
            vIF: (obj) => obj.model != true && (getters['empresa.get'].segment.reports).fields.is_model != '' 
            },
            {label: 'report_model_id', type: 'select', labelInput: 'Modelo', textBy: 'anomaly', valueBy: 'id', trackBy: 'id', allowCreate: true, vIF: (obj) => obj.model == true, 
                options: ({getters}) => {
                    return getters['reportsModel.get']
                },
                change: (objeto) => {
                    let modelo = objeto.filter(e=>e.id==this.report_model_id)
                    if (modelo.length == 0){
                        this.anomaly = null
                        this.criticality = null
                        this.recommended_action = null
                        return
                    }
                    this.anomaly = modelo[0].anomaly
                    this.criticality = modelo[0].criticality
                    this.recommended_action = modelo[0].recommended_action
                },
                searchCallback: ({dispatch, valor}) => dispatch('reportsModel.load', valor)
            },
            {label: 'anomaly', type: 'text', labelInput: 'Descrição'},
            {label: 'ativo_id', type: 'select', textBy: (e) => `${e.description} (${e.patrimony})`, valueBy: 'id', trackBy: 'id', 
            labelInput: (getters['empresa.get'].segment.reports).fields.ativo,
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            options: ({getters}) => {
                return getters['ativos.getSelect']
            }},
            {label: 'criticality', type: 'select', options: () => ['Urgente', 'Importante', 'Média', 'Baixa'], 
            labelInput: 'Criticidade', vIF: () =>  (getters['empresa.get'].segment.reports).fields.criticality != ''},
            {
                label: 'category_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', 
                labelInput: 'Categoria', options: ({getters}) => {return getters['categorias.get']['reports']}
            },
            {label: 'recommended_action', type: 'text', labelInput: (getters['empresa.get'].segment.reports).fields.recommended_action},
            {label: 'is_model', type: 'switch', labelInput: 'Deseja tornar esse report um Modelo?', permission: '2',  
            vIF: (obj) => obj.model != true && (getters['empresa.get'].segment.reports).fields.is_model != '' },
        ]
        this.listaAtualizacao = [
            "Geral", 
            ...this.listaCriacao,
            {label: 'notes', type: 'cards', labelInput: 'Comentários',
            options: ({getters}) => {
                return getters['membros.get']
            }},
            "Planejamento",
            {label: 'Planejamento', type: 'string',
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_description != ''    
            },
            {label: 'plan_description', type: 'text', labelInput: (getters['empresa.get'].segment.reports).fields.plan_description, 
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_description != '' },
            {label: 'plan_coust', type: 'text', labelInput: (getters['empresa.get'].segment.reports).fields.plan_coust, 
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_coust != ''        
            },
            "Impacto",
            {label: 'Impacto', type: 'string',
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_financial_impact != ''    
            },
            {label: 'financial_impact', type: 'number', labelInput: (getters['empresa.get'].segment.reports).fields.financial_impact,
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_financial_impact != '' 
            },
            {label: 'qualitative_impact', type: 'text', labelInput: (getters['empresa.get'].segment.reports).fields.qualitative_impact,
            vIF: () => (getters['empresa.get'].segment.reports).fields.plan_qualitative_impact != ''     
            },
            " ",
            /*{label: 'finished', type: 'select', textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Situação',
            options: () => [{'id': false, 'label': 'Aberto'}, {'id': true, 'label': 'Resolvido'}]},*/
            {label: 'canceled', type: 'buttom', labelInput: 'Enviar cancelamento do report?', permission: '2', 
            vIF: (obj) => obj.sgs == 'Em aberto' && (getters['empresa.get'].segment.reports).fields.is_model != ''
        }   , // apenas para permissões menor que 2
        ]
        this.listaAtualizacao = this.listaAtualizacao.filter(e=>e.label!='is_model')
        this.listaAtualizacao = this.listaAtualizacao.filter(e=>e.label!='model')
        this.concat = ['status']
    }

    async create({getters}){
        if(this.is_model == null) this.is_model = false
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_report`, obj)
    }

    async update({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/update_report/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_report/${this.id}`)
    }

    async finishReport({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/finish_report/${this.id}`, obj)
    }

    // Evidencias

    async getAllPhotos({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_report_photos/${this.id}`)
        return res.data.data.map((obj)=>{
            return {
                ...obj,
                url: `${url}/thumb/reports/${obj.img}?c=${id_e}&s=${this.id}&h=250&w=250`
            }
        })
    }

    async deletePhoto({getters, id_image}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_report_image/${id_image}`)
    }

    async savePhoto({getters, blob, type, content_type='image'}){

        const formData = new FormData()
        formData.append('file', blob)
        formData.append('report_id', this.id)
        formData.append('type', type)
        formData.append('content_type', content_type)
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_report_image`, formData, {
            headers: {
                "Content-Type": "multipart/form-data"
            }
        }).then(function (response) {
            console.log(response.data);
        }).catch(function (error) {
            console.log(error);
        });
    }



    async getNotes({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_report_notes/${this.id}`)
        
        if (res.data) this.notes = res.data.data.map((e)=>new ReportComentario(e, this.axios))
    }
}

export default Report