import { url, } from '@/configs/global'
import model from './__model__'

class Segmento extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'segmento'

        this.listaAtualizacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
        ]
        this.listaCriacao = this.listaAtualizacao
    }

    async create(){
        const obj = this.filterCreate()
        return await this.axios.post(`${url}/api/save_segment`, obj)
    }

    async update(){
        const obj = this.filterUpdate()
        return await this.axios.put(`${url}/api/update_segment/${this.id}`, obj)
    }
}

export default Segmento