
import { url, } from '@/configs/global'
import model from './__model__'

class Licenca extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'licenca'
        this.listaCriacao = [
            {label: 'license_id', type: 'select', textBy: (e) =>`${e.description} (R$${e.price})`, valueBy: 'id', trackBy: 'id', labelInput: 'Licença',
            options: ({getters}) => {
                return getters['licencas.get'].filter(e=>e.id < 100)
            }},
        ]
        this.listaAtualizacao = this.listaCriacao[1]


    }
    async create({getters}){
        const id_e = getters['empresa.get'].id
        await this.axios.post(`${url}/api/${id_e}/company_license`, {
            company_id : id_e,
            license_id : this.license_id,
        })
    }
    
    async update({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/company_license/${this.id}`, obj)
    }
    
}

export default Licenca