import { url, } from '@/configs/global'
import model from './__model__'

class Usuario extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'usuario'
        this.listaCriacao = [
            {label: 'name', type: 'text', labelInput: 'Usuário'},
            {label: 'email', type: 'text', labelInput: 'EMAIL'}
        ]
        this.listaAtualizacao = [
            ...this.listaCriacao,
            {label: 'level_permission', type: 'select',  textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Nível de Permissão',
            options: () => [{'id': 0, 'label': 'Administrador Master'}, {'id': 1, 'label': 'Administrador Secundário'}, {'id': 100, 'label': 'Usuário comum'}]},
            {label: 'status', type: 'select',  textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Status',
            options: () => [{'id': 1, 'label': 'Ativo'}, {'id': 0, 'label': 'Inativo'}]},
        ]
        if (this.url_imagem)
            this.photo = `${url}/thumb/users/${this.url_imagem}?c=${this.id}&h=250&w=250`
    }

    async create(){
        return await this.axios.post(`${url}/register`, {...this})
    }

    async update(){
        return await this.axios.post(`${url}/api/update_user/${this.id}`, {...this})
    }
    
    static async getLoggedUser(axios){
        const res = await axios.get(`${url}/api/get_logged_user`)
        return res.data
    }

    static async getAllCompanies(axios){
        const res = await axios.get(`${url}/api/get_companies`)
        return res.data.data
    }

    static async reset_password(axios, obj){
        const res = await axios.post(`${url}/api/reset_password`, obj)
        return res.data.message
    }

    async deletePhoto(){
        return await this.axios.delete(`${url}/api/delete_user_image`)
    }

    async savePhoto({blob}){

        const formData = new FormData()
        formData.append('file', blob)
        return await this.axios.post(`${url}/api/save_user_image`, formData, {
            headers: {
                "Content-Type": "multipart/form-data"
            }
        })
    }
}

export default Usuario