import Membro from '@/models/membro'
import { url } from '@/configs/global'

export default {
    state: () => ({
        membros: [],
        membro: {},
        permissao: 999
    }),
    mutations: {
        'membros.set': function(state, {membros, permissao, membro}){
            if(membros != undefined) state.membros = membros
            if(permissao != undefined) state.permissao = permissao
            if(membro != undefined) state.membro = membro
        },
    },
    getters: {
        'membros.get': function(state){
            return state.membros
        },
        'membro.getPermissao': function(state){
            return state.permissao
        },
        'membro.get': function(state){
            return state.membro
        }
    },
    actions: {
        'membros.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading membros")
			this.axios.get(`${url}/api/${id_e}/getall_members_company`)
			.then((response) => {
                let membros = response.data.data
                membros = membros.filter(obj => {
                    if (
                        obj.level_permission == 101 && getters['usuario.get'].level_permission >= 100
                    ) return false
                    return true
                })
                membros = membros.map(obj => new Membro({...obj.user, member_id: obj.id, level_permission: obj.level_permission}, this.axios, getters)).sort((a, b) => a.name.localeCompare(b.name))
                const membro = membros.filter(e=>e.id==getters['usuario.get'].id)
                commit('membros.set', {
                    membros: membros,
                    membro: membro.length > 0 ? membro[0] : {},
                    permissao: membro.length > 0 ? membro[0].level_permission : 999
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}