//import { url } from '@/configs/global'

export default {
    state: () => ({
        relatorios: [],
    }),
    mutations: {
        'relatorios.set': function(state, {relatorios}){
            if(relatorios != undefined) state.relatorios = relatorios
        },
    },
    getters: {
        'relatorios.get': function(state){
            return state.relatorios
        }
    },
    actions: {
        'relatorios.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			commit('relatorios.set', {
                relatorios: [
                    {nome: 'Relatório de ' + (getters['empresa.get'].segment.ativos).description_plural, tipo: 'ativos' },
                    {nome: 'Relatório de ' + (getters['empresa.get'].segment.services).description_plural, tipo: 'services'  },
                    {nome: 'Relatório de ' + (getters['empresa.get'].segment.reports).description_plural, tipo: 'reports'  },
                    {nome: 'Relatório de ' + (getters['empresa.get'].segment.calleds).description_plural, tipo: 'chamados'  },
                    {nome: 'Relatório de ' + (getters['empresa.get'].segment.inspections).description_plural, tipo: 'inspecoes'  },
                ]
            })
			setTimeout(() => {
                dispatch('loading.hide');
            }, 300);
        }
    }
}