import { url, } from '@/configs/global'
import model from './__model__'
import InspecaoSection from './inspecao_section'
import InspecaoSectionItem from './inspecao_section_item'

class Inspecao extends model{

    static DADOS = [
        {subtitle: 'finalizados', color: 'var(--verde)', number: (resume) => resume.total_finalizado, icon:'travel_explore', generalState: 0},
        {subtitle: 'em aberto', color: 'var(--laranja)', number: (resume) => resume.total_em_aberto, icon:'travel_explore', generalState: 1},
        {subtitle: 'cancelados', color: '#181a21', number: (resume) => resume.total_cancelado, icon:'travel_explore', generalState: 2},
    ]

    constructor(data, axios, getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'inspecao'
        this.model_name = (getters['empresa.get'].segment.inspections).description
        this.allowRemove = true
        this.bug_select()
        
        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
            {label: 'ativo_id', type: 'select', textBy: (e) => `${e.description} (${e.patrimony})`, valueBy: 'id', trackBy: 'id',
            options: ({getters}) => getters['ativos.getSelect'],
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            labelInput: (getters['empresa.get'].segment.inspections).fields.ativo
            },
            {label: 'related_checklist_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id',
            options: ({getters}) => getters['checklists.get'],
            labelInput: (getters['empresa.get'].segment.inspections).fields.checklist,
            searchCallback: ({dispatch, valor}) => dispatch('checklists.load', `description=${valor}`),}, 
        ]
        this.listaAtualizacao = [
            "Geral",
            ...this.listaCriacao,
            {label: 'service_id', type: 'select', textBy: (e) => `${e.request} (${e.id})`, valueBy: 'id', trackBy: 'id',
            options: ({getters}) => getters['servicos.get'],
            labelInput: (getters['empresa.get'].segment.inspections).fields.service_id,
            searchCallback: ({dispatch, valor}) => dispatch('servicos.load', `description=${valor}`),
            },
        ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_inspection`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_inspection/${this.id}`)
    }

    async update({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_inspection/${this.id}`, obj)
    }

    async getChildren({getters}){
        
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_inspection_section/${this.id}`)
        const children = await Promise.all(res.data.data.map(async(obj)=>{
            let section = new InspecaoSection(obj, this.axios)
            let res_items = await this.axios.get(`${url}/api/${id_e}/get_inspection_item/${section.id}`)
            section.items = await Promise.all(res_items.data.data.map(async(item)=>new InspecaoSectionItem(item,this.axios) ))
            return section
        }))
        return children
    }
}

export default Inspecao