<template>
	<div class="actions-buttons">
		<va-button color="secondary" flat icon="edit" @click="update(this.objeto)" size="small" />
		<div class="actions-buttons-content" v-if="showButtons || mostrarDiv">
			<va-button v-if="['Ativos', 'Reports', 'Ordens de Serviço'].includes(aba.nome)" color="secondary" flat icon="image" @click="getAllPhotos(this.objeto)" size="small" />
			<va-button v-if="['Inspeções'].includes(aba.nome)" color="secondary" flat icon="visibility" @click="showInspecao(this.objeto)" size="small" />
			<va-button v-if="['Checklists', 'Planos'].includes(aba.nome)" flat color="secondary" size="small" icon="visibility" @click="showChildren(this.objeto)"></va-button>
			<va-button v-if="['Ordens de Serviço'].includes(aba.nome)" flat color="secondary" size="small" icon="settings" @click="showServicoConfig(this.objeto)"></va-button>
			<va-badge class="mr-6 badge"  v-if="['Chamados', 'Reports', 'Ordens de Serviço', 'Ativos'].includes(aba.nome)" overlap transparent  :text="getComment(this.objeto)">
				<va-button v-if="['Chamados', 'Reports', 'Ordens de Serviço', 'Ativos'].includes(aba.nome)" flat color="secondary" size="small" icon="comment" @click="openComentarios(this.objeto)"></va-button>
			</va-badge>
			<va-button v-if="['Reports'].includes(aba.nome)" flat color="secondary" size="small" icon="check_circle" @click="showReportsFinish(this.objeto)"></va-button>
			<va-button v-if="['Ordens de Serviço'].includes(aba.nome)" flat color="secondary" size="small" icon="draw" @click="assinar(this.objeto)"></va-button>
			<va-button v-if="['Ordens de Serviço'].includes(aba.nome)" flat color="secondary" size="small" icon="print" @click="baixarOS(this.objeto)"></va-button>
			<va-button v-if="['Reports'].includes(aba.nome)" flat color="secondary" size="small" icon="print" @click="baixarReport(this.objeto)"></va-button>
			<va-button v-if="['Formulários'].includes(aba.nome)" flat color="secondary" size="small" icon="visibility" @click="responderFormulario(this.objeto)" ></va-button>
			<va-button v-if="['Formulários'].includes(aba.nome)" flat color="secondary" size="small" icon="print" @click="baixarFormulario(this.objeto)" ></va-button>
			<va-button v-if="['Inspeções'].includes(aba.nome)" flat color="secondary" size="small" icon="print" @click="baixarInspecao(this.objeto)"></va-button>
			<va-button v-if="['admin-empresas'].includes(aba.tipo)" flat color="secondary" size="small" icon="payments" @click="openFinanceiro()"></va-button>
			
			<!--<va-button v-if="['Chamados'].includes(aba.nome)  && getPermissaoMembro < 2" flat color="secondary" size="small" icon="event_available" @click="showChamadoAprov(this.objeto)"></va-button>-->
			
			<!--<va-button v-if="aba.tipo=='inspecoes'" @click="showInspecao(this.objeto)" size="small" >Abrir</va-button>-->
		</div>
	</div>
</template>

<script>
import { url_relatorio } from '@/configs/global'
export default {
	name: 'TabelaGeralActionButtons',
	props : ['objeto', 'dispatch', 'store', 'showButtons'],
	data() {
		return {
			mostrarDiv: false
		};
	},
	computed:{	
        aba() {
            const index = this.$store.getters["abas.getIndices"][this.$route.path];
            const abas = this.$store.getters["abas.getAbas"][this.$route.path];
            return index != undefined && abas != undefined ? abas[index] : {};
        },
	},
	methods:{
		verificarTamanhoTela(){
			this.mostrarDiv = window.innerWidth >= 601;
		},
		openFinanceiro(){
			if (this.store == 'admin') return
			this.$store.commit('admin.set', {
				financeiroShow: true,
				financeiroObjeto: this.objeto
			});
			this.$emit('close')
		},
		openComentarios(objeto){
			if (this.store == 'comentarios') return
			this.$store.commit('comentarios.set', { show: true, objeto: objeto, })
			this.$emit('close')
		},
		getComment(objeto){
			let number = 0
			if(objeto.appointment){
				number = objeto.appointment.length
			}
			else if(objeto.notes){
				number = objeto.notes.length
			}
			return number
		},
		assinar(objeto){
			this.$store.commit('assinatura.set', { show: true, objeto: objeto })
			this.$emit('close')
		},
		update(objeto){
			if (this.dispatch == 'edit-modal.change_editShow') return
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                callback: this.aba.callback
			})
			this.$emit('close')
		},
		async getAllPhotos(objeto){
			if (this.store == 'evidencias') return
			this.$store.commit('evidencias.set', { show: true, objeto })
			this.$emit('close')
		},
		async showChildren(obj){  
			if (this.store == 'documentos') return
			this.$store.dispatch('loading.show')
			let objetos = await obj.getChildren({getters: this.$store.getters})
			this.$store.commit('documentos.set', { show: true, objetos, parente: obj, aba: this.aba})
			this.$store.dispatch('loading.hide')
			this.$emit('close')
		},
		async responderFormulario(obj){ 
			// if (this.store == 'documentos') return
			this.$store.dispatch('loading.show')
			let formularioItems = await obj.getChildren({getters: this.$store.getters})
			console.log(formularioItems)
			this.$store.commit('formulariosShow.set', { show: true, objeto: obj, objetos: formularioItems})
			this.$store.dispatch('loading.hide')
			this.$emit('close')
		},
		async showServicoConfig(obj){
			if (this.store == 'servicos') return
			if(!obj.subtasks) await obj.loadSubtasks({getters: this.$store.getters})
			this.$store.commit('servicos.set', {configShow: true, configObjeto: obj});
			this.$emit('close')
		},
		showChamadoAprov(obj){
			this.$store.commit('chamadosShow.set', { show: true, objeto: obj})
			this.$emit('close')
		},
		showReportsFinish(obj){
			if (this.store == 'reports') return
			this.$store.commit( 'reports.set', {show: true, objeto: obj});
			this.$emit('close')
		},
		baixarOS(obj){
			if (confirm("Deseja baixar o PDF da OS?") == true) {
				const url_download = `${url_relatorio}/api/${obj.company_id}/ordem_servico/${obj.id}`
				window.open(url_download, "_blank");
			}
		},
		baixarReport(obj){
			if (confirm("Deseja baixar o PDF do report?") == true) {
				const url_download = `${url_relatorio}/api/${obj.company_id}/pdf_report/${obj.id}`
				window.open(url_download, "_blank");
			}
		},
		baixarInspecao(obj){
			if (confirm("Deseja baixar o PDF da inspeção?") == true) {
				const url_download = `${url_relatorio}/api/${obj.company_id}/pdf_inspecao/${obj.id}`
				window.open(url_download, "_blank");
			}
		},
		baixarFormulario(obj){
			if (confirm("Deseja baixar o PDF do formulário?") == true) {
				const url_download = `${url_relatorio}/api/${obj.company_id}/pdf_formulario/${obj.id}`
				window.open(url_download, "_blank");
			}
		},
		async showInspecao(objeto){
			this.$store.dispatch('loading.show');
			const secoes = await objeto.getChildren({getters: this.$store.getters})
			this.$store.dispatch('loading.hide');
			this.$store.commit('inspecoesShow.set', { show: true, objeto, secoes })
			this.$emit('close')
		}
	},
	mounted() {
		window.addEventListener('resize', this.verificarTamanhoTela);
		this.verificarTamanhoTela();
	},
	unmounted() {
		window.removeEventListener('resize', this.verificarTamanhoTela);
	},
}
</script>

<style scoped>

.actions-buttons {
	display: flex;
	flex-direction: row;
	align-content: center;
	align-items: center;
	justify-content: center;
	
}

.badge{
	--va-badge-font-size: 0.45rem;
}

.actions-buttons-content {
		display: flex;
	}

@media only screen and (max-width: 600px) {
	.actions-buttons {
		display: flex;
		flex-direction: row;
	}

}

</style>