import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        resumo: {}
    }),
    mutations: {
        'dashboard.setResumo': function(state, {chave, valor}){
            state.resumo[chave] = valor
        },
    },
    getters: {
        'dashboard.getResumo': function(state){
            return state.resumo
        }
    },
    actions: {
        'dashboard.loadResumo': async function( { dispatch, getters, commit}, rota){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            try {
                const response = await this.axios.get(`${url}/api/${id_e}/${rota}?${get_queries(getters, 'dashboard')}`)
                commit('dashboard.setResumo', {
                    chave: rota,
                    valor: response.data.data
                })
            } catch (error) {
                //dispatch('toast.error', error)
            }
            dispatch('loading.hide')
        }
    }
}