import { url, } from '@/configs/global'
import model from './__model__'
import ServicoComentario from './servico_comentario'
import ServicoCusto from './servico_custo'
import ServicoMaoDeObra from './servico_maodeobra'
import ServicoSubtask from './servico_subtask'

class Servico extends model{

    static DADOS = [
        {subtitle: 'concluídos', color: 'var(--verde)', number: (resume) => resume.total_concluido, icon:'construction', generalState: 3},
        {subtitle: 'em aberto', color: 'var(--laranja)', number: (resume) => resume.total_em_aberto, icon:'construction', generalState: 1},
        {subtitle: 'não programados', color: 'var(--roxo)', number: (resume) => resume.total_nao_programado, icon:'construction', generalState: 0},
        {subtitle: 'em atraso', color: 'var(--rosa)', number: (resume) => resume.total_atrasado, icon:'construction', generalState: 2},
        {subtitle: 'cancelados', color: '#181a21', number: (resume) => resume.total_cancelado, icon:'construction', generalState: 4},
        {subtitle: 'enviados para conclusão', color: 'var(--azul)', number: (resume) => resume.total_enviado_conclusao, icon:'construction', generalState: 5},
    ]

    constructor(data, axios,  getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.solicitacao = this.request
        this.model_type = 'servico'
        this.model_name = (getters['empresa.get'].segment.services).description
        this.allowRemove = true
        this.bug_select()
        if (this.interested_list) this.interested_list = JSON.parse(this.interested_list)
        if (this.notes)    this.notes    = this.notes.map((e)=>new ServicoComentario(e, this.axios))
        if (this.custos)    this.custos  = this.custos.map((e)=>new ServicoCusto(e, this.axios))
        if (this.mao_obra)    this.mao_obra  = this.mao_obra.map((e)=>new ServicoMaoDeObra(e, this.axios))

        this.sgs = this.canceled == 1? 'Cancelado' : this.finished_approved ==1 ? 'Concluído' : this.finish == 1? 'Enviado para conclusão' :  this.dt_end_scheduled == null?  'Não programado': new Date(this.dt_end_scheduled) > new Date() ? 'Em Aberto': 'Atrasado'

        this.listaCriacao = [
            {label: 'solicitacao', type: 'text', 
            labelInput: (getters['empresa.get'].segment.services).fields.request,
            },
            {label: 'ativo_id', type: 'select', textBy: (e) => `${e.description} (${e.patrimony})`, valueBy: 'id', trackBy: 'id', 
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            labelInput: (getters['empresa.get'].segment.services).fields.ativo,
            options: ({getters}) => {
                return getters['ativos.getSelect']
            }},
            {label: 'dt_start_scheduled', type: 'datetime', labelInput: 'Início'},
            {label: 'dt_end_scheduled', type: 'datetime', labelInput: 'Fim'},
            {label: 'responsible_id', type: 'select', textBy: (e) => `${e.name} (${e.function})`, valueBy: 'id', trackBy: 'id', 
            labelInput: (getters['empresa.get'].segment.services).fields.responsible,
            options: ({getters}) => {
                return getters['mantenedores.get']
            }},
            {label: 'project_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', 
            labelInput: (getters['empresa.get'].segment.services).fields.project,
            options: ({getters}) => {
                return getters['projetos.get']
            }},
            {label: 'maintenance_type_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', 
            labelInput: (getters['empresa.get'].segment.services).fields.maintenance_type,
            options: ({getters}) => {
                return getters['categorias.get']['servicos']
            }},
            {label: 'interested_list', type: 'option-list', textBy: (e) => `${e.name}`, valueBy: 'id', trackBy: 'id', 
            labelInput: 'Interessados',
            options: ({getters}) => {
                return getters['membros.get'].filter(e=> e.level_permission < 101)
            }},
            /*'Comentários',
            {label: 'notes', type: 'cards',  labelInput: 'Novo comentário', 
            options: ({getters}) => {
                return getters['membros.get']
            }},*/
        ]
        this.listaAtualizacao = [
            /*'Geral',*/
            ...this.listaCriacao,
            /*'Tarefas',
            {label: 'subtasks', type: 'checkbox'},
            'Custos',
            {label: 'custos', type: 'cards'},
            'Mão de obra',
            {label: 'mao_obra', type: 'cards', labelInput: 'Novo mantenedor',
            options: ({getters}) => {
                return getters['mantenedores.get']
            }},
            ' ',*/
            /*{label: 'priority', type: 'select', options: () => ['Urgente', 'Importante', 'Média', 'Baixa'], labelInput: 'Prioridade'},
            {label: 'finish', type: 'select', textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Situação',
            options: () => [{'id': true, 'label': 'Finalizado'}, {'id': false, 'label': 'Não finalizado'}]},*/
            {label: 'finish', type: 'buttom', labelInput: 'Enviar finalização do serviço?', permission: '100', vIF: (obj) =>obj.sgs=='Em aberto'|| obj.sgs=='Em Aberto'||obj.sgs=='Atrasado'},
            {label: 'undo_finish', type: 'buttom', labelInput: 'Enviar reabertura da ordem de serviço?', permission: '2', vIF: (obj) => obj.sgs=='Enviado para conclusão'},
            {label: 'finished_approved', type: 'buttom', labelInput: 'Enviar aprovação e conclusão do serviço?', permission: '2', vIF: (obj) => obj.sgs == 'Enviado para conclusão'}, // apenas para permissões menor que 2
            {label: 'canceled', type: 'buttom', labelInput: 'Enviar cancelamento do serviço?', permission: '2', vIF: (obj) => obj.sgs!='Concluído'&&obj.sgs!='Cancelado'}, // apenas para permissões menor que 2
            {label: 'canceling_reason', type: 'select', labelInput: 'Motivo do cancelamento', permission: '2', vIF: (obj) => obj.canceled == true, textBy: 'description', valueBy: 'id', trackBy: 'id', 
            options: ({getters}) => {
                return getters['categorias.get']['cancelamentoOS']
            }}, // apenas para permissões 
            {label: 'canceling_note', type: 'text', labelInput: 'Comentário de cancelamento', permission: '2', vIF: (obj) => obj.canceled == true}, // apenas para permissõesmenor que 2
        ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        // console.log(obj.interested_list)
        obj.interested_list = JSON.stringify(obj.interested_list)
        console.log(obj)
        return await this.axios.post(`${url}/api/${id_e}/save_service`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        if((!this.mao_obra || this.mao_obra.length == 0) && this.finish == true && getters['empresa.get'].segment_id != 2){ 
            this.finish = false 
            throw 'Informe a mão de obra para fechar o serviço.'
        }
        obj.interested_list = JSON.stringify(obj.interested_list)
        console.log(obj)
        return await this.axios.patch(`${url}/api/${id_e}/update_service/${this.id}`, obj)
    }

    async approveFinish({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        if(this.finished_approved == true && this.finish == false){ 
            this.finished_approved = false 
            throw 'O serviço precisa estar finalizado para ser aprovado.'
        }
        else return await this.axios.patch(`${url}/api/${id_e}/finish_approve_service/${this.id}`, obj)
    }

    async finishServiceById({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        if((!this.mao_obra || this.mao_obra.length == 0) &&  getters['empresa.get'].segment_id != 2){ 
            this.finish = false 
            throw 'Informe a mão de obra para fechar o serviço.'
        }
        else return await this.axios.patch(`${url}/api/${id_e}/finish_service/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service/${this.id}`)
    }

    // Evidencias

    async getAllPhotos({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_service_photos/${this.id}`)
        return res.data.data.map((obj)=>{
            return {
                ...obj,
                url: `${url}/thumb/services/${obj.img}?c=${id_e}&s=${this.id}&h=250&w=250`
            }
        })
    }

    async deletePhoto({getters, id_image}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service_image/${id_image}`)
    }

    async savePhoto({getters, blob, type, description, content_type='image'}){

        const formData = new FormData()
        formData.append('file', blob)
        formData.append('service_id', this.id)
        formData.append('order', 1)
        if (type) formData.append('type', type)
        else formData.append('type', 0)
        if (description) formData.append('description', description)
        formData.append('content_type', content_type)
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_service_image`, formData, {
            headers: {
                "Content-Type": "multipart/form-data"
            }
        }).then(function (response) {
            console.log(response.data);
        }).catch(function (error) {
            console.log(error);
        });
    }

    async loadSubtasks({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_service_subtasks/${this.id}`)
        this.subtasks = res.data.data.map((e)=>new ServicoSubtask(e, this.axios))
    }

    async removeAllTasks({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service_subtasks_mass/${this.id}`)
    }

    async getNotes({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_service_notes/${this.id}`)
        
        if (res.data) this.notes = res.data.data.map((e)=>new ServicoComentario(e, this.axios))
    }

}

export default Servico