import Plano from '@/models/plano'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        planos: [],
        last_page: 1
    }),
    mutations: {
        'planos.set': function(state, {planos, last_page}){
            if(planos != undefined) state.planos = planos
            if(last_page != undefined) state.last_page = last_page
        },
    },
    getters: {
        'planos.get': function(state){
            return state.planos
        },
        'planos.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'planos.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading planos")
			this.axios.get(`${url}/api/${id_e}/get_plan_by_company_id?${get_queries(getters, args)}`)
			.then((response) => {
                commit('planos.set', {
                    last_page: response.data.data.last_page,
                    planos: response.data.data.data.map(obj => new Plano(obj, this.axios))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}