import Servico from '@/models/servico'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        show: false,
        servicos: [],
        servicos_calendario: [],
        configObjeto: {},
        configShow: false,
        last_page: 1
    }),
    mutations: {
        'servicos.set': function(state, {servicos, show, configObjeto, configShow, last_page, servicos_calendario}){
            if(servicos != undefined) state.servicos = servicos
            if(show != undefined) state.show = show
            if(configObjeto != undefined) state.configObjeto = configObjeto
            if(configShow != undefined) state.configShow = configShow
            if(last_page != undefined) state.last_page = last_page
            if(servicos_calendario != undefined) state.servicos_calendario = servicos_calendario
        },
    },
    getters: {
        'servicos.get': function(state){
            return state.servicos
        },
        'servicos.getCalendario': function(state){
            return state.servicos_calendario
        },
        'servicos.getCalendar': function(state){
            return state.show
        },
        'servicos.getConfigObjeto': function(state){
            return state.configObjeto
        },
        'servicos.getConfigShow': function(state){
            return state.configShow
        },
        'servicos.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'servicos.load': async function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading servicos")
            console.log(get_queries(getters, args), args)
			await this.axios.get(`${url}/api/${id_e}/get_service?${get_queries(getters, args)}`)
			.then((response) => {
                commit('servicos.set', {
                    last_page: response.data.data.last_page,
                    servicos: response.data.data.data.map(obj => new Servico(obj, this.axios, getters))//.sort((a, b) => a.request.localeCompare(b.request))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'servicos.loadCalendario': async function( { dispatch, getters, commit}, {dtStart, dtEnd}){
			const id_e = getters['empresa.get'].id
            console.log("loading servicos calendario")
            if (!dtStart || !dtEnd){
                const today = new Date()
                this.dtStart = `${today.getFullYear()}-${today.getMonth().month + 1 < 10 ? '0' : ''}${today.getMonth().month + 1 }-01 00:00:00`
                this.dtEnd   = `${today.getMonth().month + 1 ==12? today.getFullYear() + 1 : today.getFullYear()}-${today.getMonth().month + 1 <  9 ? '0' : ''}${today.getMonth().month + 1 == 12 ? '01' : today.getMonth().month + 2}-01 00:00:00`
            }
			await this.axios.get(`${url}/api/${id_e}/get_service_calendar?dtStart=${dtStart}&dtEnd=${dtEnd}`)
			.then((response) => {
                commit('servicos.set', {
                    servicos_calendario: response.data.data.map(obj => new Servico(obj, this.axios, getters)).sort((a, b) => a.request.localeCompare(b.request))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			})
        },
        'servicos.calendar': function({commit}){
            commit( 'servicos.set', {show: true} );
        },
        'servicos.calendarChange': function({commit},value){
            commit( 'servicos.set', {show: value} );
        },
        'servicos.showConfig': function({commit}, configObjeto){
            commit( 'servicos.set', {configShow: true, configObjeto});
        },
    }
}