import { url, } from '@/configs/global'
import ChamadoComentario from './chamado_comentario'
import model from './__model__'

class Chamado extends model{

    static DADOS = [
        {subtitle: 'concluídos', color: 'var(--verde)', number: (resume) => resume.total_concluido, icon: 'call', generalState: 2 },
        {subtitle: 'em aberto', color: 'var(--laranja)', number: (resume) => resume.total_em_aberto, icon: 'call', generalState: 0},
        {subtitle: 'em andamento', color: 'var(--azul)', number: (resume) => resume.total_em_andamento, icon: 'call', generalState: 1},
        {subtitle: 'rejeitados', color: 'var(--rosa)', number: (resume) => resume.total_rejeitado , icon: 'call', generalState: 3}
    ]

    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'chamado'
        this.allowRemove = true
        this.bug_select()

        if (this.notes)    this.notes    = this.notes.map((e)=>new ChamadoComentario(e, this.axios))

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição', inputType: 'textarea'},
            {label: 'ativo_id', type: 'select', textBy: (e) => `${e.description} (${e.patrimony})`, valueBy: 'id', trackBy: 'id', labelInput: 'Ativo',
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            options: ({getters}) => {
                return getters['ativos.getSelect']
            }},
            {label: 'assigned_to', type: 'select', textBy: 'name', valueBy: 'id', trackBy: 'id', labelInput: 'Atribuir a ',
            options: ({getters}) => {
                return getters['membros.get'].filter(e=>e.level_permission < 2)
            }},
        ]
        this.listaAtualizacao = [
            ...this.listaCriacao.slice(0,2),
            {label: 'approved', type: 'buttom', labelInput: 'Enviar aprovação do chamado?', permission: '2', vIF: (obj) => obj.sgs == 'Em aberto'},
            {label: 'finished', type: 'buttom', labelInput: 'Enviar finalização do chamado?', permission: '2', vIF: (obj) => obj.sgs == 'Em andamento' },
        ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_called`, obj)
    }

    async update({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_called/${this.id}`, obj)
    }

    async approve({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/approved_or_finished_called/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_called/${this.id}`)
    }

    async getNotes({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_called_notes/${this.id}`)
        
        if (res.data) this.notes = res.data.data.map((e)=>new ChamadoComentario(e, this.axios))
    }
}

export default Chamado