import Usuario from '../models/usuario'
import Empresa from '../models/empresa'
import Ativo from '../models/ativo'
import Report from '../models/report'
import Servico from '../models/servico'
import Setor from '../models/setor'
import Tag from '../models/tag'
import Projeto from '../models/projeto'
import Mantenedor from '../models/mantenedor'
import Membro from '../models/membro'
import PlanoFrequencia from '../models/plano_frequencia'
import ChecklistSection from '../models/checklist_section'
import Plano from '../models/plano'
import Checklist from '../models/checklist'
import ChecklistSectionItem from '../models/checklist_section_item'
import PlanoFrequenciaItem from '../models/plano_frequencia_item'
import AtivoComentario from '../models/ativo_comentario'
import ServicoSubtask from '../models/servico_subtask'
import ServicoComentario from '../models/servico_comentario'
import ServicoCusto from '../models/servico_custo'
import ServicoMaoDeObra from '../models/servico_maodeobra'
import Inspecao from '../models/inspecao'
import Chamado from '../models/chamado'
import Categoria from '../models/categoria'
import Segmento from '../models/segmento'
import Licenca from '../models/licenca'
import Formulario from '../models/formulario'
import FormularioItem from '../models/formulario_item'

export default {
    state: () => ({
        editShow: false,
        editObjeto: {},
        callback: null,
        callbackOptions: null,
        action: null
    }),
    mutations: {
        'edit-modal.set': function(state, {editShow, editObjeto, callback, callbackOptions, action}){
            if(editShow != undefined) state.editShow = editShow
            if(editObjeto != undefined) state.editObjeto = editObjeto
            if(callback != undefined) state.callback = callback
            if(callbackOptions != undefined) state.callbackOptions = callbackOptions
            if(action != undefined) state.action = action
        },
        'edit-modal.setActionNull': function(state){
            state.action = null
        }
    },
    getters: {
        'edit-modal.getEditShow': function(state){
            return state.editShow
        },
        'edit-modal.getEditObjeto': function(state){
            return state.editObjeto
        },
        'edit-modal.getCallback': function(state){
            return state.callback
        },
        'edit-modal.getCallbackOptions': function(state){
            return state.callbackOptions
        },
        'edit-modal.getAction': function(state){
            return state.action
        }
    },
    actions: {
		'edit-modal.editShow': function({commit, getters}, {objeto, callback, callbackOptions, id_parente, action, is_admin}){
            if (objeto == 'usuario')     objeto = new Usuario ({  }, this.axios)
            if (objeto == 'empresa')     objeto = new Empresa ({  }, this.axios)
            if (objeto == 'ativo')     objeto = new Ativo ({ status: 1 }, this.axios, getters)
            if (objeto == 'ativo_comentario')     objeto = new AtivoComentario ({}, this.axios)
            if (objeto == 'report')     objeto = new Report ({ status: 1 }, this.axios, getters)
            if (objeto == 'servico')     objeto = new Servico ({ }, this.axios, getters)
            if (objeto == 'servico_subtask')     objeto = new ServicoSubtask ({ service_id: id_parente }, this.axios)
            if (objeto == 'servico_comentario')     objeto = new ServicoComentario ({ service_id: id_parente }, this.axios)
            if (objeto == 'servico_custo')     objeto = new ServicoCusto ({ service_id: id_parente }, this.axios)
            if (objeto == 'servico_maodeobra')     objeto = new ServicoMaoDeObra ({ service_id: id_parente }, this.axios)
            if (objeto == 'setor')     objeto = new Setor ({ }, this.axios)
            if (objeto == 'tag')     objeto = new Tag ({ }, this.axios)
            if (objeto == 'projeto')     objeto = new Projeto ({ }, this.axios, getters)
            if (objeto == 'mantenedor')     objeto = new Mantenedor ({ }, this.axios, getters)
            if (objeto == 'membro')     objeto = new Membro ({ }, this.axios, getters)
            if (objeto == 'plano')     objeto = new Plano ({ }, this.axios)
            if (objeto == 'plano_frequencia')     objeto = new PlanoFrequencia ({ plan_id: id_parente }, this.axios)
            if (objeto == 'plano_frequencia_item')     objeto = new PlanoFrequenciaItem ({ frequency_id: id_parente }, this.axios)
            if (objeto == 'checklist')     objeto = new Checklist ({ }, this.axios, getters)
            if (objeto == 'checklist_section')     objeto = new ChecklistSection ({ checklist_id: id_parente }, this.axios)
            if (objeto == 'checklist_section_item')     objeto = new ChecklistSectionItem ({ checklist_section_id: id_parente }, this.axios)
            if (objeto == 'inspecao')     objeto = new Inspecao ({ }, this.axios,  getters)
            if (objeto == 'chamado')     objeto = new Chamado ({company_id: getters['empresa.get'].id}, this.axios)
            if (objeto == 'formulario')     objeto = new Formulario ({company_id: getters['empresa.get'].id}, this.axios, getters)
            if (objeto == 'formulario_item')     objeto = new FormularioItem ({company_id: getters['empresa.get'].id,  form_id: id_parente}, this.axios)
            if (objeto == 'categoria')     objeto = new Categoria ({ company_id: getters['empresa.get'].id, is_admin, public: !!is_admin }, this.axios)
            if (objeto == 'segmento')     objeto = new Segmento ({ }, this.axios)
            if (objeto == 'licenca')     objeto = new Licenca ({ }, this.axios)
            if (!action) commit('edit-modal.setActionNull')
            commit('edit-modal.set', { editShow: true, editObjeto: objeto, callback, callbackOptions, action })
        },
		'edit-modal.change_editShow': function( {commit}, value){
            commit('edit-modal.set', { editShow: value })
        }
    }
}