import Checklist from '@/models/checklist'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        checklists: [],
        last_page: 1
    }),
    mutations: {
        'checklists.set': function(state, {checklists, last_page}){
            if(checklists != undefined) state.checklists = checklists
            if(last_page != undefined) state.last_page = last_page
        },
    },
    getters: {
        'checklists.get': function(state){
            return state.checklists
        },
        'checklists.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'checklists.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading checklists")
			this.axios.get(`${url}/api/${id_e}/get_checklist?${get_queries(getters, args)}`)
			.then((response) => {
                commit('checklists.set', {
                    last_page: response.data.data.last_page,
                    checklists: response.data.data.data.map(obj => new Checklist(obj, this.axios, getters))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}