import { url, get_queries } from '@/configs/global'
import Categoria from '@/models/categoria'

export default {
    state: () => ({
        CATEGORIAS: {},
    }),
    mutations: {
        'categoria.set': function(state, {key, value}){
            state.CATEGORIAS[key] = value
        }
    },
    getters: {
        'categorias.get': function(state){
            return state.CATEGORIAS
        },
        'categorias.getAll': function(state){
            const cat = state.CATEGORIAS
            let array = []
            if (cat.ativos)      array.push(...cat.ativos)
            if (cat.servicos) array.push(...cat.servicos)
            if (cat.reports) array.push(...cat.reports)
            if (cat.cancelamentoOS) array.push(...cat.cancelamentoOS)
            return array
        }
    },
    actions: {
        'categorias.load': function( { getters, dispatch, commit}, args){
            dispatch('loading.show');
            const id_e = getters['empresa.get'].id
            const keys = ["servicos", "ativos", "reports", "cancelamentoOS"]
            for (let i = 0; i < keys.length; i++) {
                const key = keys[i];          
                let api = key == 'servicos' ? `get_service_maintenance_type` : `get_categories/${key}`
                this.axios.get(`${url}/api/${api}?company_id=${id_e}&${get_queries(getters, args)}`)
                .then((response) => {
                    commit('categoria.set', { 
                        key, 
                        value: response.data.data.map(e => new Categoria({
                            ...e,
                            base: key == 'servicos' ? 'servicos' : e.base
                        }, this.axios))
                    })
                }).catch((err)=>{
                    dispatch('toast.error', err);
                }).finally(()=>{
                    dispatch('loading.hide');
                })

            }
        }
    }
}