import Notificacao from '@/models/notificacao'
import { url } from '@/configs/global'

export default {
    state: () => ({
        notificacoes: [],
    }),
    mutations: {
        'notificacoes.set': function(state, {notificacoes}){
            if(notificacoes != undefined) state.notificacoes = notificacoes
        },
    },
    getters: {
        'notificacoes.get': function(state){
            return state.notificacoes
        }
    },
    actions: {
        'notificacoes.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
            console.log('loading notificacoes')
			const id_u = getters['usuario.get'].id
			this.axios.get(`${url}/api/${id_u}/get_notification`)
			.then((response) => {
                commit('notificacoes.set', {
                    notificacoes: response.data.data.map(obj => new Notificacao (obj, this.axios)).sort((a, b) => a.description.localeCompare(b.description))
                })
                console.log(response)
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}