import Report from '@/models/report'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        reports: [],
        reportsModel: [],
        last_page: 1,
        show: false,
        objeto: {}
    }),
    mutations: {
        'reports.set': function(state, {reports, last_page, show, objeto, reportsModel}){
            if(reports != undefined) state.reports = reports
            if(reportsModel != undefined) state.reportsModel = reportsModel
            if(last_page != undefined) state.last_page = last_page
            if(show != undefined) state.show = show
            if(objeto != undefined) state.objeto = objeto
        }
    },
    getters: {
        'reports.get': function(state){
            return state.reports
        },
        'reportsModel.get': function(state){
            return state.reportsModel
        },
        'reports.getLastPage': function(state){
            return state.last_page
        },
        'reports.getShowFinish': function(state){            
            return state.show
        },
        'reports.getFinishObjeto': function(state){
            return state.objeto
        },
    },
    actions: {
        'reports.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading reports")
			this.axios.get(`${url}/api/${id_e}/get_reports?${get_queries(getters, args)}`)
			.then((response) => {
                commit('reports.set', {
                    last_page: response.data.data.last_page,
                    reports: response.data.data.data.map(obj => new Report(obj, this.axios,  getters))//.sort((a, b) => a.anomaly.localeCompare(b.anomaly))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'reportsModel.load': function( { dispatch, getters, commit}, description){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading reportsModel")
			this.axios.get(`${url}/api/${id_e}/get_reports?isModel=true${description? '&description=' + description : '' }`)
			.then((response) => {
                commit('reports.set', {
                    last_page: response.data.data.last_page,
                    reportsModel: response.data.data.data.map(obj => new Report(obj, this.axios,  getters))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
    }
}