<template>
   	<div class="edit-modal-titulo">
		<span class="button-close">
			<va-button icon="close" size="1.5em" flat text-color="#f53d3d" color="cinza" @click="close"/><br>
		</span>
		{{title}}
		<TabelaGeralActionButtons v-if="!hidebuttons" :objeto="objeto" @close="close" :store="store" :dispatch="dispatch" :showButtons="true"/>
	</div>
</template>

<script>
	import TabelaGeralActionButtons from './TabelaGeralActionButtons'
	export default {
		name: 'ModalTitle',
		components:{TabelaGeralActionButtons, },
		props: ['dispatch', 'store', 'title', 'objeto', 'hidebuttons'],
		methods:{
			close(){
				if (this.dispatch){
					this.$store.dispatch(this.dispatch, false);
				}
				if (this.store){
					let obj = {
						configShow: false,
						financeiroShow: false
					}
					if (this.store != 'servicos') obj.show = false
					this.$store.commit(`${this.store}.set`, obj);
				}
			},
		}
	}
</script>

<style scoped>
.edit-modal-titulo{
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 25px;
	text-transform: uppercase; 
	color: var(--branco);
	font-size: 1.1rem;
	font-weight: bold;
	flex-wrap: wrap;
}

.button-close {
	display: none;
}

@media only screen and (max-width: 600px) {
	.button-close {
		width: 100%;
		display: flex;
		justify-content: flex-end;
		padding-bottom: 15px;
	}
}
</style>