import Ativo from '@/models/ativo'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        ativos: [],
        ativos_parents: [],
        ativos_with_children: [],
        ativos_select: [],
        ativos_categorias: [],
        last_page: 1
    }),
    mutations: {
        'ativos.set': function(state, {ativos, ativos_with_children, last_page, ativos_select, ativos_categorias, ativos_parents}){
            if(ativos != undefined) state.ativos = ativos
            if(last_page != undefined) state.last_page = last_page
            if(ativos_with_children != undefined) state.ativos_with_children = ativos_with_children
            if(ativos_select != undefined) state.ativos_select = ativos_select
            if(ativos_categorias != undefined) state.ativos_categorias = ativos_categorias
            if(ativos_parents != undefined) state.ativos_parents = ativos_parents
        },
    },
    getters: {
        'ativos.get': function(state){
            return state.ativos
        },
        'ativos.getLastPage': function(state){
            return state.last_page
        },
        'ativos.getWithChildren': function(state){
            return state.ativos_with_children
        },
        'ativos.getSelect': function(state){
            return state.ativos_select
        },
        'ativos.getAtivosParents': function(state){
            return state.ativos_parents
        },
        'ativos.getCategorias': function(state){
            return state.ativos_categorias
        },
    },
    actions: {
        'ativos.load': async function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
            console.log("loading ativos")
            console.log(get_queries(getters, args))
			const id_e = getters['empresa.get'].id
			await this.axios.get(`${url}/api/${id_e}/get_ativos_alternative?${get_queries(getters, args)}`)
			.then((response) => {
                commit('ativos.set', {
                    last_page: response.data.data.last_page,
                    ativos: response.data.data.data.map(obj => new Ativo(obj, this.axios, getters)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
            console.log('fim')
        },
        'ativos.loadWithChildren': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_ativos_with_children`)
			.then((response) => {
                commit('ativos.set', {
                    ativos_with_children: response.data.data.map(obj => new Ativo(obj, this.axios, getters)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'ativos.loadSelect': function( { dispatch, getters, commit}, description){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_ativos_alternative${description? '?description=' + description : '' }&limit=50`)
			.then((response) => {
                commit('ativos.set', {
                    ativos_select: response.data.data.data.map(obj => new Ativo(obj, this.axios, getters)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'ativos.loadAtivosParents': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_ativos_parents?limit=10000`)
			.then((response) => {
                console.log(response.data.data)
                commit('ativos.set', {
                    ativos_parents: response.data.data.data.map(obj => new Ativo(obj, this.axios, getters)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'ativos.loadCategorias': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_ativos_category`)
			.then((response) => {
                commit('ativos.set', {  ativos_categorias: response.data.data })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
    }
}