const format = (date) => {
    const year = date.getFullYear()
    const month = date.getMonth()+1
    const day = date.getDate()
    return `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day}`
}

module.exports = {
    'url': "https://api.px40.com.br",
    'url_relatorio': "https://api.px40.com.br",
    // 'url': "https://px40-api.cme-eng.com.br",
    // 'url_relatorio': "https://px40-api.cme-eng.com.br",
    // 'url': "http://127.0.0.1:8000",
    // 'url_relatorio': "http://127.0.0.1:8000",
	'color': ["#3CDE93","#FFDDAA","#3CB3DE", "#F85B40", "#000000",'#BA55D3', '#91cc75', '#a60303', '#73c0de','#9a60b4', '#ea7ccc'],
    'get_queries': function(getters, tipo){
        const filtros = getters['abas.getFiltro']
        let objeto  = filtros && filtros[tipo] ? filtros[tipo] : {}
        if (tipo == 'dashboard') objeto['isModel'] = false
        objeto['limit'] = 25
        return Object.keys(objeto).reduce(function (previous, key) {
            if (objeto[key]!=undefined && objeto[key]!=null){
                if(key == 'dtStart' || key=='dtEnd'){
                    return `${previous}&${key}=${format(new Date(objeto[key]))}` ;
                }else{
                    return `${previous}&${key}=${objeto[key]}` ;
                }
            } else {
                return previous;
            }
        }, "").replace('&', '')
    },
}