import { url, } from '@/configs/global'
import model from './__model__'
import FormularioItem from './formulario_item'

class Formulario extends model{
    constructor(data, axios, getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'formulario'
        this.allowRemove = true
        this.bug_select()
        this.model_name = (getters['empresa.get'].segment.forms).description
        this.copyChildren=[]
        

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição', inputType: 'textarea'},
            {label: 'ativo_id', type: 'select', textBy: (e) => `${e.description} (${e.patrimony})`, valueBy: 'id', trackBy: 'id', labelInput: 'Ativo',
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            options: ({getters}) => {
                return getters['ativos.getSelect']
            }},
            {label: 'model', type: 'switch', labelInput: 'Carregar um modelo?', permission: '100', 
            vIF: (obj) =>( obj.model != true && this.is_model != true)
            }, 
            {label: 'related_form_id', type: 'select', labelInput: 'Modelo', textBy: 'description', valueBy: 'id', trackBy: 'id', allowCreate: true, vIF: (obj) => (obj.model == true ), disabled: true,
            options: ({getters}) => {
                return getters['formulariosModel.get']
            },
            change: async (objeto) => {
                let modelo = objeto.filter(e=>e.id==this.related_form_id)
                if (modelo.length == 0){
                    this.description = null
                    // this.ativo_id = null
                    return
                }
                this.description = modelo[0].description
                this.copyChildren = await modelo[0].getChildren({getters})
            },
            searchCallback: ({dispatch, valor}) => dispatch('formulariosModel.load', valor)
            },
            {label: 'is_model', type: 'switch', labelInput: 'Deseja tornar esse formulário um Modelo?', permission: '2',  
            vIF: (obj) => (obj.model != true && this.is_model != true) },
        ]
        this.listaAtualizacao = [
            ...this.listaCriacao,
        ]
        this.listaAtualizacao = this.listaAtualizacao.filter(e=>e.label!='model' && e.label!='is_model')
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        console.log(this.copyChildren)
        let res = await this.axios.post(`${url}/api/${id_e}/save_form`, obj)
        if (res.data.data){
            for (let i = 0; i < this.copyChildren.length; i++) {
                let new_obj = new FormularioItem({
                    form_id: res.data.data.id,
                    description: this.copyChildren[i].description
                }, this.axios)
                await new_obj.create({getters})
            }
        }
        return res 
    }

    async update({getters}){
        const obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_form/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_form/${this.id}`)
    }

    async getChildren({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_form_items/${this.id}`)
        const children = await Promise.all(res.data.data.map(async(obj)=>{
            return new FormularioItem(obj, this.axios)
        }))
        return children
    }
}

export default Formulario