import { url, } from '@/configs/global'
import model from './__model__'
import AtivoComentario from './ativo_comentario'
import AtivoTag from './ativo_tag'

class Ativo extends model{
    constructor(data, axios, getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                if(['electrical_features','mechanical_features','work_shift', 'coordinates'].includes(key)){
                    for (const property in data[key]){
                        this[property] = data[key][property]
                    }
                }
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'ativo'
        this.model_name = (getters['empresa.get'].segment.ativos).description
        this.allowRemove = true
        this.bug_select_keys = ['parent_ativo']
        this.bug_select()

        if (this.appointment)    this.notes    = this.appointment.map((e)=>new AtivoComentario(e, this.axios))

        this.listaCriacao = [
            {label: 'description', type: 'text', 
            labelInput: (getters['empresa.get'].segment.ativos).fields.description
            },
            {label: 'patrimony', type: 'text', 
            labelInput: (getters['empresa.get'].segment.ativos).fields.patrimony
            },
            {label: 'category_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', 
            labelInput: (getters['empresa.get'].segment.ativos).fields.category,
            options: ({getters}) => {return getters['categorias.get']['ativos']}
            },
            {label: 'parent_ativo_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', labelInput: 'Ativo Pai',
            searchCallback: ({dispatch, valor}) => dispatch('ativos.loadSelect', valor),
            options: ({getters}) => { return getters['ativos.getSelect']}, 
            vIF: () => (getters['empresa.get'].segment.ativos).fields.parent_ativo != ''},
        ]
        this.listaAtualizacao = [
            {label: 'Geral', type: 'string'},
            ...this.listaCriacao,
            {label: 'sector_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id',
            labelInput: (getters['empresa.get'].segment.ativos).fields.sector,
            options: ({getters}) => { return getters['setores.get']},
            vIF: () => (getters['empresa.get'].segment.ativos).fields.parent_ativo != ''
            },
            {label: 'branch', type: 'text', labelInput: (getters['empresa.get'].segment.ativos).fields.branch},
            {label: 'model', type: 'text', labelInput: (getters['empresa.get'].segment.ativos).fields.model},
            {label: 'serie', type: 'text', labelInput: (getters['empresa.get'].segment.ativos).fields.serie},
            {label: 'Características Elétricas', type: 'string', 
            vIF: () => (getters['empresa.get'].segment.ativos).fields.electrical_features != ''
            },
            {label: 'power', type: 'number', labelInput: 'Potência',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.electrical_features != ''
            },
            {label: 'power_unity', type: 'select', textBy: 'label', valueBy:'id', trackBy: 'id', labelInput: 'Unidade de medida ',
            options: ()=>   [{id : 'kw', label: 'kW'},
                            {id : 'cv', label: 'CV'},
                            {id : 'kva', label: 'kVA'}],
            vIF: () => (getters['empresa.get'].segment.ativos).fields.electrical_features != ''
            },
            {label: 'voltage', type: 'number', labelInput: 'Tensão',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.electrical_features != ''    
            },
            {label: 'voltage_type', type: 'select', textBy: 'label', valueBy:'id', trackBy: 'id', labelInput: 'Alimentação ',
            options: ()=>   [{id : 'monofasico', label: 'Monofásico'},
                            {id : 'bifasico', label: 'Bifásico'},
                            {id : 'trifasico', label: 'Trifásico'},
                            {id : 'cc', label: 'CC'}],
            vIF: () => (getters['empresa.get'].segment.ativos).fields.electrical_features != ''
            },
           
            {label: 'Características Mecânicas', type: 'string',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.mechanical_features != ''    
            },
            {label: 'width', type: 'number', labelInput: 'Largura (m)',  
            vIF: () => (getters['empresa.get'].segment.ativos).fields.mechanical_features != ''
            },
            {label: 'height', type: 'number', labelInput: 'Altura (m)',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.mechanical_features != ''
            },
            {label: 'length', type: 'number', labelInput: 'Comprimento (m)',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.mechanical_features != ''
            },
            {label: 'weight', type: 'number', labelInput: 'Peso (kg)',
            vIF: () => (getters['empresa.get'].segment.ativos).fields.mechanical_features != ''
            },
            {label: '  ',  type: 'string'},
            {label: 'comments', type: 'textarea', 
            labelInput: (getters['empresa.get'].segment.ativos).fields.comments
            },
            {label: 'status', type: 'select', textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Situação',
            options: () => [{'id': 1, 'label': 'Ativado'}, {'id': 0, 'label': 'Desativado'}]},
            {label: 'Coordenadas', type: 'string', 
            vIF: (obj) => ((getters['empresa.get'].segment.ativos).fields.coordinates != '' && obj.altitude == undefined) 
            },
            {label: 'latitude', labelInput: 'coordenadas . latitude' , type: 'text',
            vIF: (obj) => ((getters['empresa.get'].segment.ativos).fields.coordinates != '' && obj.altitude == undefined)    
            },
            {label: 'longitude', labelInput: 'coordenadas . longitude' , type: 'text',
            vIF: (obj) => ((getters['empresa.get'].segment.ativos).fields.coordinates != '' && obj.altitude == undefined)  
            },
        ]

        this.concat = ['status']
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/cad_ativo`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        if (obj.power || obj.power_unity || obj.voltage || obj.voltage_type || obj.electrical_features) {
            obj.electrical_features = JSON.stringify({
                power:       obj.power ? parseFloat(obj.power.toString().replace(',','.')) : null,
                power_unity: obj.power_unity ? obj.power_unity : null,
                voltage:     obj.voltage ? parseFloat(obj.voltage.toString().replace(',','.')) : null,
                voltage_type: obj.voltage_type ? obj.voltage_type : null,
            })
        }
        if (obj.width || obj.height || obj.length || obj.weight || obj.mechanical_features) {
            obj.mechanical_features = JSON.stringify({
                width:  obj.width  ? parseFloat(obj.width.toString().replace(',','.'))  : null,
                height: obj.height ? parseFloat(obj.height.toString().replace(',','.')) : null,
                length: obj.length ? parseFloat(obj.length.toString().replace(',','.')) : null,
                weight: obj.weight ? parseFloat(obj.weight.toString().replace(',','.')) : null,
            })
        }
        if ((obj.latitude && obj.longitude) && (obj.altitude == undefined) ) {
            obj.coordinates = JSON.stringify({
                latitude: obj.latitude ? parseFloat(obj.latitude.toString().replace(',','.'))  : null,
                longitude: obj.longitude ? parseFloat(obj.longitude.toString().replace(',','.'))  : null,
            })
        }
        if (obj.work_shift) {
            obj.work_shift = JSON.stringify(obj.work_shift)
        }
        console.log(obj)
        return await this.axios.put(`${url}/api/${id_e}/update_ativo/${this.id}`, obj)
    }
    
    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_ativo/${this.id}`)
    }

    // Evidencias

    async getAllPhotos({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_ativo_photos/${this.id}`)
        console.log(res)
        return res.data.data.map((obj)=>{
            return {
                ...obj,
                url: `${url}/thumb/ativos/${obj.img}?c=${id_e}&s=${this.id}`
            }
        })
    }

    async deletePhoto({getters, id_image}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_ativo_image/${id_image}`)
    }

    async savePhoto({getters, blob, content_type='image'}){
        const formData = new FormData()
        console.log(blob)
        formData.append('file', blob)
        formData.append('ativo_id', this.id)
        formData.append('order', 1)
        formData.append('content_type', content_type)
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_ativo_image`, formData, {
            headers: {
                "Content-Type": "multipart/form-data"
            }
        }).then(function (response) {
            console.log(response.data);
        }).catch(function (error) {
            console.log(error);
        });
    }
    async getNotes({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_ativo_appointments/${this.id}`)
        if (res.data) this.notes = res.data.data.map((e)=>new AtivoComentario(e, this.axios))
    }

    async getTags({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_ativo_tags/${this.id}`)
        if (res.data) this.tag = res.data.data.map((e)=>new AtivoTag(e, this.axios))
    }

    async getChildren({getters}){
        const id_e = getters['empresa.get'].id
        const res = await this.axios.get(`${url}/api/${id_e}/get_ativos_alternative?parentAtivoId=${this.id}&limit=10000`)
		this.children = res.data.data.data.map(obj => new Ativo(obj, this.axios, getters)).sort((a, b) => a.description.localeCompare(b.description))
    }
}

export default Ativo