import { url, } from '@/configs/global'
import model from './__model__'

class AtivoComentario extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'ativo_comentario'
        this.allowRemove = true

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
            {label: 'value', type: 'number', labelInput: 'Valor'},
            {label: 'unity', type: 'text', labelInput: 'Unidade'},
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['ativo_id', ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_ativo_appointment`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_ativo_appointment/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_ativo_appointment/${this.id}`)
    }
}

export default AtivoComentario