import { url, } from '@/configs/global'
import model from './__model__'

class AtivoTag extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'ativo_tag'
        this.allowRemove = true

        this.listaCriacao = [
            {label: 'tag_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', 
            labelInput: 'TAGS',
            options: ({getters}) => {return getters['categorias.get']['ativos']}
            }
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['ativo_id', ]
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_ativo_tag`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_ativo_tag/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_ativo_tag/${this.id}`)
    }
}

export default AtivoTag