import Formulario from '@/models/formulario'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        objetos: [], // formulários
        formulariosModel: [], // formulário modelos
        last_page: 1,
        show: false, // controle do modal de formulários
        objeto: {}, // fomulário do modal
        formularioItems: {}, // itens do formulário (Children)
    }),
    mutations: {
        'formularios.set': function(state, {objetos, last_page, formulariosModel}){
            if(objetos != undefined) state.objetos = objetos
            if(last_page != undefined) state.last_page = last_page
            if(formulariosModel != undefined) state.formulariosModel = formulariosModel
        },
        'formulariosShow.set': function(state, {show, objeto, objetos}){
            if(show != undefined) state.show = show
            if(objeto != undefined) state.objeto = objeto
            if(objetos != undefined) state.formularioItems = objetos
            console.log(state.show)
        }
    },
    getters: {
        'formulariosModel.get': function(state){
            return state.formulariosModel
        },
        'formularios.get': function(state){
            return state.objetos
        },
        'formularios.getLastPage': function(state){
            return state.last_page
        },
        'formulariosShow.getShow': function(state){
            return state.show
        },
        'formulariosShow.getObjeto': function(state){
            console.log(state.objeto)
            return state.objeto
        },
        'formulariosShow.getItems': function(state){
            return state.formularioItems
        }
    },
    actions: {
        'formularios.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading formularios")
            console.log(get_queries(getters, args))
			this.axios.get(`${url}/api/${id_e}/get_forms?${get_queries(getters, args)}`)
			.then((response) => {
                commit('formularios.set', {
                    last_page: response.data.data.last_page,
                    objetos: response.data.data.data.map(obj => new Formulario(obj, this.axios, getters))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'formulariosModel.load': function( { dispatch, getters, commit}, description){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading formulariosModel")
			this.axios.get(`${url}/api/${id_e}/get_forms?isModel=true${description? '&description=' + description : '' }`)
			.then((response) => {
                commit('formularios.set', {
                    last_page: response.data.data.last_page,
                    formulariosModel: response.data.data.data.map(obj => new Formulario(obj, this.axios, getters))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
    }
}