import Inspecao from '@/models/inspecao'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        show: false,
        inspecoes: [],
        last_page: 1, 
        secoes: [], 
        objeto: [],
    }),
    mutations: {
        'inspecoes.set': function(state, {inspecoes, last_page}){
            if(inspecoes != undefined) state.inspecoes = inspecoes
            if(last_page != undefined) state.last_page = last_page
        },
        'inspecoesShow.set': function(state, { show, objeto, secoes}){
            if(show != undefined) state.show = show
            if(objeto != undefined) state.objeto = objeto
            if(secoes != undefined) state.secoes = secoes
            console.log(state.show)
        },
    },
    getters: {
        'inspecoes.get': function(state){
            return state.inspecoes
        },
        'inspecoes.getLastPage': function(state){
            return state.last_page
        },
        'inspecoes.getShow': function(state){
            return state.show
        },
        'inspecoes.getSecoes': function(state){
            return state.secoes
        },
        'inspecoes.getObjeto': function(state){
            return state.objeto
        },
    },
    actions: {
        'inspecoes.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log("loading inspecoes")
			this.axios.get(`${url}/api/${id_e}/get_inspection?${get_queries(getters, args)}`)
			.then((response) => {
                commit('inspecoes.set', {
                    last_page: response.data.data.last_page,
                    inspecoes: response.data.data.data.map(obj => new Inspecao(obj, this.axios, getters))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}