//import Membro from '@/models/membro'
import { url } from '@/configs/global'

export default {
    state: () => ({
        licencas: [],
        licencas_empresa:[], 
        show: false,
    }),
    mutations: {
        'licencas.set': function(state, {licencas, licencas_empresa, show}){
            if(licencas != undefined) state.licencas = licencas
            if(licencas_empresa != undefined) state.licencas_empresa = licencas_empresa
            if(show != undefined) state.show = show
        },
    },
    getters: {
        'licencas.get': function(state){
            return state.licencas
        },
        'licencas_empresa.get': function(state){
            return state.licencas_empresa
        },
        'licencas.getShow': function(state){            
            return state.show
        },
    },
    actions: {
        'licencas_empresa.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
            console.log(id_e)
            //console.log(this.axios.get(`${url}/api/${id_e}/company_license`))
			this.axios.get(`${url}/api/${id_e}/company_license`)
			.then((response) => {
                let licencas_empresa = response.data.data
                commit('licencas.set', {
                    licencas_empresa: licencas_empresa,
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
        'licencas.load': function( { dispatch, commit}){
            //onsole.log(this.axios.get(`${url}/api/license`))
			this.axios.get(`${url}/api/license`)
			.then((response) => {
                let licencas = response.data.data
                commit('licencas.set', {
                    licencas: licencas
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        },
    }
}