import { url, } from '@/configs/global'
import model from './__model__'

class Notificacao extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'notificacao'
        this.allowRemove = true

        this.listaCriacao = []
        this.listaAtualizacao = this.listaCriacao
    }

    // async create({getters}){
    //     const obj = this.filterCreate()
    //     const id_e = getters['empresa.get'].id
    //     return await this.axios.post(`${url}/api/${id_e}/save_notification`, obj)
    // }

    async update({getters}){
        const obj = this.filterUpdate()
        const id_u = getters['usuario.get'].id
        return await this.axios.put(`${url}/api/${id_u}/update_notification/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_notification/${this.id}`)
    }
}

export default Notificacao