import { url, } from '@/configs/global'
import model from './__model__'

class Membro extends model{
    constructor(data, axios, getters) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'membro'
        let options = [
            {id : 0, label: 'Administrador'},
            {id : 1, label: 'Programador'}, // gestor
            {id : 2, label: 'Membro'},
            {id : 100, label: 'Convidado'},
            {id : 200, label: 'Inativo'},
        ]
        if (getters && getters['usuario.get'] && getters['usuario.get'].level_permission < 100){
            options.push({id : 101, label: 'Convidado Oculto'})
        }
        this.listaCriacao = [
            {label:'email', type:'text'},
            {label: 'level_permission', type: 'select', textBy: 'label', valueBy:'id', trackBy: 'id', labelInput: 'Nível de permissão ',
            options: () =>   options
            },
        ]
        this.listaAtualizacao = [
            this.listaCriacao[1]
        ]
        this.allowRemove = false
    }
    async create({getters}){
        const id_e = getters['empresa.get'].id
        return this.axios.post(`${url}/api/${id_e}/cad_user_company`, {
            company_id : id_e,
            email : this.email,
            level_permission: this.level_permission
        })
    }

    async update({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_user_company/${this.member_id}`, {
            level_permission: this.level_permission
        })
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_user_company/${this.member_id}`)
    }
}

export default Membro