import Tag from '@/models/tag'
import { url } from '@/configs/global'

export default {
    state: () => ({
        tags: [],
    }),
    mutations: {
        'tags.set': function(state, {tags}){
            if(tags != undefined) state.tags = tags
        },
    },
    getters: {
        'tags.get': function(state){
            return state.tags
        }
    },
    actions: {
        'tags.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_company_tag`)
			.then((response) => {
                commit('tags.set', {
                    tags: response.data.data.map(obj => new Tag (obj, this.axios)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}