export default {
    state: () => ({
        steps: {
            'workspaces': workspaces,
            'ativos': ativos,
            'mantenedores': mantenedores,
            'servicos': servicos,
        },
        tours: {
            'ativos': {},
            'mantenedores': {},
            'servicos': {},
        },
        next: null,
    }),
    mutations: {
        'tutorial.setTours': function(state, tours){
            state.tours = {
                ...state.tours,
                ...tours
            }
        },
        'tutorial.setNext': function(state, next){
            state.next = next
            console.log(state.next)
        }
    },
    getters: {
        'tutorial.getTours': function(state){
            return state.tours
        },
        'tutorial.getSteps': function(state){
            return state.steps
        },
        'tutorial.getNext': function(state){
            return state.next
        },
    },
    actions: {
        'tutorial.next': async function({getters, commit}, {tour, valor}){
            const myTour = getters['tutorial.getTours'][tour]
            console.log(myTour)
            if (myTour && myTour.isLast){
                commit('tutorial.setNext',
                    {
                        'workspaces': 'ativos',
                        'ativos': 'mantenedores',
                        'mantenedores': "servicos",
                        'servicos': ""
                    }[tour]
                )
                console.log('ok 1')
                return setTimeout(() => {
                    return myTour.stop()
                }, 500);
            }
            if (myTour && myTour.currentStep != undefined && myTour.currentStep == valor){
                console.log('ok 2')
                return setTimeout(() => {
                    myTour.nextStep()
                }, 500);
            }
        },        
    }
}